.main-image{
    position: relative;
    display: block;
    width: 100%;
    height: auto;
    max-width: 1597px;
    margin: 0px auto;
    padding-bottom: 30px;
    }
img{
    vertical-align: initial !important;
    }
.swedishPrinceHeader{
    position: relative;
    margin: 0px auto;
    height: auto;
    width: min-content;
    margin-top: 0px;
    margin-bottom: 10px;
    color: #fd1d86;
    }
.swedishPrinceHeader h1>*{
    position: relative;
    display: block;
    padding: 0px !important;
    font-size: 72px;
    line-height: 1em;
    }
.swedishPrinceHeader h1 .adj1{
    margin-top: -6px;
    font-size: 60px;
    left: -11px;
    }
.swedishPrinceHeader h1 .adj2{
    margin-top: 0px;
    left: -8px;
    }
.swedishPrinceHeader h1 .adj3{
    margin-top: -4px;
    left: 0px;
    }
.swedish-prince-text-block-1{
    position: relative;
    padding: 42px 60px 48px 60px;
    }
.swedish-prince-text-block-2{
    position: relative;
    display: inline-block;
    padding: 42px 72px 37px 72px;
    }
.swedish-prince-text-block-2.adj-out{
    padding: 42px 42px 37px 42px;
    }
