@font-face {
	font-family: 'HCo Gotham SSm Light';
	font-display: auto;
	src:url('./Webfonts/woff2/GothamSSm-Light_Web.woff2') format('woff2'),
		url('./Webfonts/woff/GothamSSm-Light_Web.woff') format('woff');
	font-weight: 300;
	font-style: normal;
}
@font-face {
	font-family: 'HCo Gotham SSm Book';
	font-display: auto;
	src:url('./Webfonts/woff2/GothamSSm-Book_Web.woff2') format('woff2'),
		url('./Webfonts/woff/GothamSSm-Book_Web.woff') format('woff');
	font-weight: 400;
	font-style: normal;
}

body{
  position: relative;
  height: 100%;
  width: 100%;
  background-color: rgb(244, 242, 237);
  color: #7c7170;
  /*font-family: montserrat, sans-serif;*/
  font-family: 'HCo Gotham SSm Light';
  font-size: 16px;
  line-height: 24px;
}

html, body, #app, #app>div {
  min-height: 100vh;
  max-height: 100vh;
}

* {
  text-decoration: none;
  list-style: none;
  border: none;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  -ms-appearance: none;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

