h1{
  font-size: 34px;
  line-height: 1em;
  font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  padding: 0px;
  margin: 0px;
  letter-spacing: -0.02em;
}
h2{
  font-size: 29px;
  line-height: 1em;
  font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  padding: 0px;
  margin: 0px;
  padding-bottom: 12px;
  letter-spacing: -0.02em;
}
h3{
  font-size: 21px;
  line-height: 1em;
  font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  padding: 0px;
  margin: 0px;
  letter-spacing: -0.02em;
}

h6{
  font-size: 18px;
  line-height: 26px;
  padding: 0px;
  margin: 0px;
  letter-spacing: -0.02em;
}
p{
  padding: 0px;
  margin: 0px;
  padding: 0px;
  margin: 0px;
}
.italics{
  font-family: bodoni-pt-variable, sans-serif;
  font-style: italic;
  font-variation-settings: "opsz" 60, "wght" 700;
}


.sml{
  display: block !important;
}
.sml-inline{
  display: inline-block !important;
}
.lrg{
  display: none !important;
}
.lrg-inline{
  display: none !important;
}
.align-flex-bottom{
  align-items: flex-end;
}
.white-text{
  color:rgb(246, 245, 242);
}
.light-pink{
  color: #f6ddda;
  }
.right-align{
  float: right;
  }
.text-align-right{
  text-align: right;
  }
main{
  position:relative;
  height: 100%;
  width: 100%;
}
.initials{
  display: block;
  position: absolute;
  right: 30px;
  bottom: 24px;
  font-size: 110px;
  color: #f6ddda;
  line-height: 1em;
  z-index: -1;
  font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  }
