.initials-small{
  display: block;
    position: absolute;
    width: 144px;
    margin-top: -90px;
    margin-left: 50px;
    z-index: 0;
  }
.e-comerce-link-1{
  position: relative;
  border-top: 1px solid #999;
  border-bottom: 1px solid #999;
  font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  width: 36%;
  margin: 26px 0px;
  transition: all ease 300ms;
  cursor: pointer;
}
.e-comerce-link-1:hover{
  color: rgb(253, 29, 134);
}
.e-comerce-link-2{
  border-top: 1px solid #eabfba;
  border-bottom: 1px solid #eabfba;
  font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  text-transform: uppercase;
  text-align: center;
  width: max-content;
  margin: 26px 0px;
  transition: all ease 300ms;
  cursor: pointer;
  padding: 6px 0px 1px 0px;
  }
  .e-comerce-link-2:hover{
    border-top: 1px solid rgb(253, 29, 134);
    border-bottom: 1px solid rgb(253, 29, 134);
    color: rgb(253, 29, 134);
    }
.content-top{
  padding-top: 144px !important;
  min-height: 100vh;
  /*max-height: 100vh;*/
  width: 100%;
  }
  .content-top.large-adjust{
    padding-top: 233px !important;
  }
.content{
  position: relative;
  display: block;
  width: 100%;
  margin: 0px auto;
  padding: 24px 36px;
  }
.content.bottom{
  padding: 0px 36px 60px 36px;
  }
.content.middle-big-pad{
  padding: 36px 36px 36px 36px;
  }
.content.full-page{
  max-width: 100%;
  padding: 24px 0px 24px 11px;
  display: flex;
  align-items: stretch;
  }
.full-screen-top {
  position: relative;
  top: 0px;
  margin-top: 0px;
  min-height: 100vh;
  max-height: 100vh;
  width: 100%;
  max-width: 100%;
  }
.half-page-left{
  display: inline-block;
  position: relative;
  width: 50%;
  padding-right: 5.5px;
  }
.half-page-right{
  display: inline-block;
  position: relative;
  width: 50%;
  padding-right: 5.5px;
  /*background-color: #ecdfdb;*/
  }
.half-page-text-block{
  position: relative;
  top: 46%;
  transform: translateY(-50%);
  }
.big-quote{
  font-size: 60px;
  line-height: 60px;
  color:#eabfba;
  font-family: bodoni-pt-variable, sans-serif;
  font-style: normal;
  font-variation-settings: "opsz" 60, "wght" 700;
  }

/* ---------- BOOTSTRAP ADJUSTMENTS ---------- */
.row{
  margin: 0px -5.5px;
  }
  .row>* {
    padding: 5.5px 5.5px;
  }

  .row.vertical-center{
    height: 100%;
  }


@media only screen and (min-width:768px){
.sml{
  display: none !important;
  }
.sml-inline{
  display: none !important;
  }
.lrg{
  display: block !important;
  }
.lrg-inline{
  display: inline-block !important;
  }
.content{
  padding: 24px 55px;
  }
.content.bottom{
  padding: 24px 55px 60px 55px;
  }
.content.middle-big-pad{
  padding: 55px 55px 55px 55px;
  }
}
@media only screen and (min-width:992px){
h1{
  font-size: 55px;
  }
h2{
  font-size: 47px;
  }
h3{
  font-size: 34px;
  }
.content{
  max-width: 843px;
  padding: 24px 0px;
  }
.content.bottom{
  padding: 24px 0px 60px 0px;
  }
.content.middle-big-pad{
  padding: 55px 0px 55px 0px;
  }
}
@media only screen and (min-width:1200px){
.content{
  max-width: 987px;
  }
  .content.middle-big-pad{
    padding: 96px 0px 96px 0px;
    }
  .big-quote{
    font-size: 72px;
    line-height: 72px;
  }  
}
@media only screen and (min-width:1400px){
h1{
  font-size: 76px;
  line-height: 64px;
  }
h2{
  font-size: 55px;
  line-height: 60px;
  }
h3{
  font-size: 47px;
  line-height: 60px;
  }
.content{
  max-width: 1220px;
  } 
.content-top{
  padding-top: 233px !important;
  }
.content-top.large-adjust{
  padding-top: 233px !important;
  }
}
@media only screen and (min-width:1550px){
.content{
  max-width: 1364px;
  }
}
@media only screen and (min-width:1800px){
.content{
  max-width: 1597px;
  }
}

@media only screen and (max-width:786px){
  .content.no-pad-sml{
    padding: 0px;
  }
  .col-clear-sml{
    display: none;
  }
}